import { mapState } from 'vuex';
export default {
	data() {
		return {
			monthsnumber:0.00,
			sumnumber:15,
			len:0,
			startX:0,
			moveEndX:0,
			valueX:0,
			ulsvisit:"hidden",
			listtext:'全部',
			listtexturl:'../../../static/navIcon/next.png',
			list:'',
		}
	},
	computed: mapState(['UserInfo']),
	onLoad(){
		var _this=this
		// this.UserInfo.userid='3052109275300000002'
		var datas=`{method:"GetUserMoneyInfo",user_id:"${this.UserInfo.userid}"}`
		this.$api.ReagisterPhone(datas).then((res)=>{
			console.log(res)
			if(res.data.result==0){
				console.log("没有余额")
				this.monthsnumber='0.00'
				return;
			}
			this.monthsnumber=JSON.parse(res.data.data_score)[0].MONEY
		})
	},
	methods: {
				touchstart(event){
					event.preventDefault();
	　　　　			this.startX = event.touches[0].pageX
				},
				touchmove(event){
					event.preventDefault();
			　　　　this.moveEndX = event.touches[0].pageX
					this.valueX= this.moveEndX - this.startX
					if ( this.valueX >= 80 ) {
							uni.navigateBack({
								delta:1,
								animationType: 'pop-in',
								animationDuration: 200
							})
			　　　　}
				},
				openuls(){
					if(this.ulsvisit=="hidden"){
						this.ulsvisit="visible"
						this.listtexturl='../../../static/navIcon/down.png'
						return
					}
					this.ulsvisit="hidden"
					this.listtexturl='../../../static/navIcon/next.png'
				},
				changetext(text){
					this.listtext=text
					console.log(this.listtext)
					this.ulsvisit="hidden"
					this.listtexturl='../../../static/navIcon/next.png'
				}
	}
}
